

.item-card:hover {
    transform: scale(1.05);
}

.item-image {
    aspect-ratio: 3/4;
    background: linear-gradient(45deg, #3b82f6, #8b5cf6);
    position: relative;
    overflow: hidden;
}

.item-image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform 0.3s ease;
}

 .item-card:hover .item-image img {
    transform: scale(1.1);
}

.item-info {
    padding: clamp(10px, 2vw, 15px);
}

.item-title {
    font-size: clamp(12px, 2vw, 14px);
    font-weight: 600;
    margin-bottom: 8px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    line-height: 1.3;
}

.item-meta {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: clamp(10px, 1.8vw, 12px);
    gap: 8px;
}

.item-meta .chapter {
    color: #fb923c;
    font-weight: 500;
}

.item-meta .time {
    color: #9ca3af;
}




/* Dark mode support */
@media (prefers-color-scheme: dark) {
    .item-card {
        background: #1f2937;
        box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
    }
}

/* Reduce motion for accessibility */
@media (prefers-reduced-motion: reduce) {
    .item-card {
        transition: none;
    }
    
    .item-card:hover {
        transform: none;
    }
    
    .item-image img {
        transition: none;
    }
    
    .item-card:hover .item-image img {
        transform: none;
    }
}