* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    overflow-x: hidden;
}

.main-content {
    padding: 30px 0;
    max-width: 1200px;
    margin: 0 auto;
}

.container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
}

.warning {
    background: #2563eb;
    padding: 15px 0;
}

.warning-content {
    display: flex;
    align-items: center;
    font-size: 14px;
}

.warning-icon {
    background: #1e40af;
    width: 24px;
    height: 24px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 10px;
    font-size: 12px;
}

.section-title {
    white-space: nowrap;
    margin-bottom: 20px;
    display: flex;
    align-items: center;
}

.section-title .arrow {
    color: #fb923c;
    margin-left: 10px;
}

/* Featured Manga Grid - Chuyển sang Flexbox */
.featured-grid {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: stretch;
    gap: clamp(8px, 2vw, 20px);
    margin-bottom: 40px;
    overflow: hidden;
    width: 100%;
    box-sizing: border-box;
}

/* Đảm bảo các item có kích thước linh hoạt và không vượt màn hình */
.featured-grid .item-card {
    flex: 0 1 calc((100% - 3 * 16px) / 4);
    width: clamp(120px, 20vw, 200px); /* giảm width lý tưởng và max */
    min-width: 120px;                 /* giảm min-width nếu cần */
    max-width: 200px;                 /* giảm max-width để vừa 4 thẻ */
    box-sizing: border-box;
}


.two-column {
    gap: 40px;
}

.asdf {
    display: flex;
    flex-direction: column;
    gap: 40px;
    justify-content: center;
}

.new-manga-grid {
    display: flex;
    gap: clamp(10px, 2vw, 20px);
    overflow-x: auto;
    overflow-y: hidden;
    white-space: nowrap;
    scroll-behavior: smooth;
    padding-bottom: 10px;
    width: 100%;
    box-sizing: border-box;
}

/* Đảm bảo item trong new-manga-grid không vượt màn hình */
.new-manga-grid .item-card {
    flex: 0 0 auto;
    width: clamp(180px, 25vw, 250px);
    min-width: 180px;
    max-width: 250px;
}

/* Scrollbar styling cho webkit browsers */
.new-manga-grid::-webkit-scrollbar {
    height: 6px;
}

.new-manga-grid::-webkit-scrollbar-track {
    background: #374151;
    border-radius: 3px;
}

.new-manga-grid::-webkit-scrollbar-thumb {
    background: #6b7280;
    border-radius: 3px;
}

.new-manga-grid::-webkit-scrollbar-thumb:hover {
    background: #9ca3af;
}

.new-manga-card {
    background: #374151;
    border-radius: 10px;
    padding: 20px;
    transition: background-color 0.3s;
}

.new-manga-card:hover {
    background: #4b5563;
}

.new-manga-content {
    display: flex;
    gap: 15px;
}

.new-manga-image {
    width: 80px;
    height: 110px;
    background: linear-gradient(45deg, #8b5cf6, #ec4899);
    border-radius: 6px;
    flex-shrink: 0;
}

.new-manga-image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 6px;
}

.new-manga-info {
    flex: 1;
}

.new-manga-title {
    font-size: 14px;
    font-weight: 600;
    margin-bottom: 10px;
}

.chapter-list {
    display: flex;
    flex-direction: column;
    gap: 5px;
}

.chapter-item {
    display: flex;
    justify-content: space-between;
    font-size: 12px;
}

.chapter-link {
    color: #fb923c;
    text-decoration: none;
    cursor: pointer;
}

.chapter-link:hover {
    color: #fdba74;
}

.rankings {
    background: #374151;
    border-radius: 10px;
    padding: 20px;
}

.ranking-tabs {
    display: flex;
    gap: 20px;
    margin-bottom: 20px;
    font-size: 14px;
}

.tab {
    color: #9ca3af;
    cursor: pointer;
    padding-bottom: 8px;
    transition: color 0.3s;
}

.tab.active {
    color: #fb923c;
    border-bottom: 2px solid #fb923c;
}

.tab:hover {
    color: white;
}

.ranking-list {
    display: flex;
    flex-direction: column;
    gap: 15px;
}

.ranking-item {
    display: flex;
    align-items: center;
    gap: 12px;
    padding: 10px;
    border-radius: 6px;
    transition: background-color 0.3s;
    cursor: pointer;
}

.ranking-item:hover {
    background: #4b5563;
}

.rank-number {
    width: 32px;
    height: 32px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 14px;
    font-weight: bold;
    flex-shrink: 0;
}

.rank-number.top3 {
    background: #fb923c;
    color: white;
}

.rank-number.other {
    background: #4b5563;
    color: #9ca3af;
}

.rank-image {
    width: 48px;
    height: 64px;
    background: linear-gradient(45deg, #3b82f6, #8b5cf6);
    border-radius: 4px;
    flex-shrink: 0;
}

.rank-image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 4px;
}

.rank-info {
    flex: 1;
    min-width: 0;
}

.rank-title {
    font-size: 14px;
    font-weight: 500;
    margin-bottom: 4px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.rank-chapter {
    font-size: 12px;
    color: #9ca3af;
    margin-bottom: 2px;
}

.rank-views {
    font-size: 12px;
    color: #6b7280;
}

/* Responsive Design với Flexbox */

/* Container chính phải responsive */
.container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 clamp(10px, 3vw, 20px);
    width: 100%;
    box-sizing: border-box;
}

/* Desktop lớn */
@media (min-width: 1200px) {
    .featured-grid {
        gap: 24px;
    }
    
    .featured-grid .item-card {
        width: clamp(160px, 20vw, 200px);
        max-width: 200px;
    }
}

/* Desktop */
@media (max-width: 1199px) and (min-width: 992px) {
    .featured-grid {
        gap: 20px;
        justify-content: center;
    }
    
    .featured-grid .item-card {
        width: clamp(160px, 22vw, 260px);
        max-width: 260px;
    }
    .two-column {
        width: 100%;
    }
}
/* @media (max-width: 1199px) and (min-width: 1099px) {
    
    
    .featured-grid .item-card {
        width: clamp(200px, 22vw, 240px);
        max-width: 240px;
    }
} */

/* Tablet landscape */
@media (max-width: 991px) and (min-width: 768px) {
    .featured-grid {
        gap: 18px;
        justify-content: center;
    }
    
    .featured-grid .item-card {
        width: clamp(200px, 25vw, 230px);
        max-width: 230px;
    }
}

/* Tablet portrait */
@media (max-width: 767px) and (min-width: 576px) {
    .asdf {
        flex-direction: column;
        gap: 20px;
        align-items: center;
    }
    
    .featured-grid {
        gap: 16px;
        justify-content: center;
    }
    
    .featured-grid .item-card {
        width: clamp(200px, 30vw, 250px);
        max-width: 250px;
    }
    
    
}

/* Mobile landscape */
@media (max-width: 575px) and (min-width: 480px) {
    .featured-grid {
        gap: 14px;
        justify-content: center;
    }
    
    .featured-grid .item-card {
        width: clamp(170px, 40vw, 200px);
        max-width: 200px;
    }
}

/* Mobile portrait */
@media (max-width: 479px) and (min-width: 400px) {
    .featured-grid {
        gap: 12px;
        justify-content: center;
    }
    
    .featured-grid .item-card {
        width: clamp(160px, 45vw, 180px);
        max-width: 180px;
    }
    
    .two-column .section-title {
        font-size: 15px;
    }
}

/* Mobile portrait nhỏ */
@media (max-width: 399px) and (min-width: 350px) {
    .featured-grid {
        gap: 10px;
        justify-content: center;
    }
    
    .featured-grid .item-card {
        flex-basis: calc((100% - 3 * 16px) / 2);
        width: clamp(180px, 100vw, 230px);
        max-width: 230px;
    }
}

/* Mobile rất nhỏ - hiển thị 1 cột */
@media (max-width: 349px) {
    .featured-grid {
        flex-direction: column;
        align-items: center;
        gap: 16px;
    }
    
    .featured-grid .item-card {
        width: calc(100vw - 40px);
        max-width: 300px;
        min-width: auto;
    }
}

/* Cải thiện responsive cho section khác */
@media (max-width: 768px) {
    .rankings {
        padding: 15px;
    }
    
    .ranking-tabs {
        gap: 15px;
        font-size: 13px;
    }
    
    .ranking-item {
        padding: 8px;
    }
    
    .rank-number {
        width: 28px;
        height: 28px;
        font-size: 12px;
    }
    
    .rank-image {
        width: 40px;
        height: 56px;
    }
}

/* Hiệu ứng hover cho mobile */
@media (hover: none) and (pointer: coarse) {
    .item-card:hover {
        transform: none;
    }
    
    .ranking-item:hover {
        background: transparent;
    }
    
    .new-manga-card:hover {
        background: #374151;
    }
}